@import url(https://unpkg.com/modern-css-reset/dist/reset.min.css);
:root {
  /* Typescale */
  --ts-ratio: 2;
  --ts-3: 0.5946035575rem;
  --ts-2: 0.7071067812rem;
  --ts-1: 0.8408964153rem;
  --ts0: 1rem;
  --ts1: 1.189207115rem;
  --ts2: 1.4142135624rem;
  --ts3: 1.6817928305rem;
  --ts4: 2rem;
  --ts5: 2.37841423rem;
}

html {
  font-size: 100%; /*16px*/
}

body {
  font-family: sans-serif;
  line-height: 1.189207115rem;
  line-height: var(--ts1);
  font-size: 1rem;
  font-size: var(--ts0);
  font-weight: 400;
  letter-spacing: 0.5px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400;
  line-height: calc(2 * 1em);
  line-height: calc(var(--ts-ratio) * 1em);
  font-family: serif;
}

h1 {
  font-size: 2.37841423rem;
  font-size: var(--ts5);
  font-weight: 300;
  letter-spacing: -1.5px;
}

h2 {
  font-size: 2rem;
  font-size: var(--ts4);
  font-weight: 300;
  letter-spacing: 0.5px;
}

h3 {
  font-size: 1.6817928305rem;
  font-size: var(--ts3);
  letter-spacing: 0px;
}

h4 {
  font-size: 1.4142135624rem;
  font-size: var(--ts2);
  letter-spacing: 0.25px;
}

h5 {
  font-size: 1.189207115rem;
  font-size: var(--ts1);
  letter-spacing: 0px;
}

h6 {
  font-size: calc(1rem + 0.7071067812rem);
  font-size: calc(var(--ts0) + var(--ts-2));
  font-weight: 500;
  letter-spacing: 0.15px;
}

button {
  font-weight: 500;
  font-size: 0.8408964153rem;
  font-size: var(--ts-1);
  letter-spacing: 1.25px;
  text-transform: uppercase;
}

nav {
  display: -webkit-flex;
  display: flex;
}

nav > ul {
  padding: 0;
  list-style: none;
  display: -webkit-flex;
  display: flex;
}

nav li {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

nav li + li {
  -webkit-margin-start: 1rem;
          margin-inline-start: 1rem;
}

.top-nav-header{
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.features {
  width: 100%;
  max-width: 45em;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

@media only screen and (min-width: 60em) {
  .features {
    max-width: 60em;
  }
}

@media only screen and (min-width: 75em) {
  .features {
    max-width: 75em;
  }
}

@media only screen and (min-width: 87.5em) {
  .features {
    max-width: 87.5em;
  }
}

.features-cards-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.features-cards-wrapper section > * + * {
  margin-top: 2rem;
}

.features-cards-wrapper > section {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  margin: 1rem;
  min-height: 582px;
  border: solid thin slategray;
  padding: 1rem;
}

.features-cards-wrapper figure > img {
  margin: auto;
  box-shadow: 0px 10px 20px 0px #7788996b;
}

.features-cards-wrapper figure > * + * {
  margin-top: 1rem;
}

.features-cards-wrapper figcaption {
  padding: 1rem;
}

.hero {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 25vh;
  overflow: hidden;
}

.hero > * {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.hero > :first-child:not(.centered) {
  margin-top: 0;
}

.hero > :last-child:not(.centered) {
  margin-bottom: 0;
}

.hero > .centered {
  margin-top: auto;
  margin-bottom: auto;
}

.frame {
  --n: 1;
  --d: 3;
  padding-bottom: calc(var(--n) / var(--d) * 100%);
  position: relative;
}

.frame > * {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.frame > img,
.frame > video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-caption {
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  background-color: rgba(255, 255, 255, 0.7);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  padding: 2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.hero-caption p {
  max-width: 54ch;
}

.hero-caption > * + * {
  margin-top: 1rem;
}

@-webkit-keyframes img-slide {
  0% {
    object-position: 10% 10%;
    opacity: 0;
  }
  1% {
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    object-position: 90% 90%;
  }
}

@keyframes img-slide {
  0% {
    object-position: 10% 10%;
    opacity: 0;
  }
  1% {
    opacity: 1;
  }
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    object-position: 90% 90%;
  }
}

.animate > img {
  object-fit: none;
  -webkit-animation: img-slide 100s linear infinite;
          animation: img-slide 100s linear infinite;
}

.how-to-play {
  width: 100%;
  max-width: 45em;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

@media only screen and (min-width: 60em) {
  .how-to-play {
    max-width: 60em;
  }
}

@media only screen and (min-width: 75em) {
  .how-to-play {
    max-width: 75em;
  }
}

@media only screen and (min-width: 87.5em) {
  .how-to-play {
    max-width: 87.5em;
  }
}

.how-to-play > section {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.how-to-play > section > ul {
  -webkit-flex-basis: 0;
          flex-basis: 0;
  -webkit-flex-grow: 999;
          flex-grow: 999;
  padding: 1rem;
}

.how-to-play > section > img {
  -webkit-flex-basis: 640px;
          flex-basis: 640px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  z-index: 1000;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  padding: 2rem;
  z-index: 1000;
  width: 80vw;
  border-radius: 0.5rem;
}


.game-screen {
  display: grid;
  width: auto;
  height: 90vh;
  place-items: center;
  border: solid 2px black;
  /* background: blue; */
}

.art-container {
  border: 2px solid blue;
  padding: 1rem;
}

.gameui-container {
  border: 2px solid red;
  padding: 1rem;
}

.multiple-choice {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
}

